import React from 'react';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import { RadioButtonGroup } from '../../molecules/RadioButtonGroup/RadioButtonGroup';
import { SingleLineTextField } from '../../atoms';
import { Box } from '@material-ui/core';

export type Props = {
  role: string | null;
  gender: number | null;
  daycareId: number | null;
  onChangeRole: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeGender: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onChangeDaycareId: (e: React.ChangeEvent<HTMLInputElement>) => void;
};

export const ChooseRole: React.FC<Props> = (props) => {
  const {
    role,
    gender,
    daycareId,
    onChangeRole,
    onChangeGender,
    onChangeDaycareId,
  } = props;

  return (
    <>
      <FormControl component="fieldset">
        <RadioGroup
          row
          aria-label="gender"
          name="gender1"
          value={role}
          onChange={onChangeRole}
        >
          <Box mb={2}>
            <FormControlLabel
              value=""
              control={<Radio />}
              label=""
            />
          </Box>
          <Box mb={2}>
            <FormControlLabel
              value=""
              control={<Radio />}
              label=""
            />
          </Box>
        </RadioGroup>
      </FormControl>
      {role == '' ? (
        <RadioButtonGroup
          value={gender}
          firstLabel=""
          secondLabel=""
          firstValue={0}
          secondValue={1}
          onChange={onChangeGender}
        />
      ) : (
        <div></div>
      )}
      {role == '' ? (
        <SingleLineTextField
          id="Daycare_ID"
          type="number"
          textName="Daycare_ID"
          placeholder=""
          isFullWidth={false}
          value={daycareId}
          onChange={onChangeDaycareId}
        />
      ) : (
        <div></div>
      )}
    </>
  );
};
