import React, { ChangeEvent, memo } from 'react';
import styled from 'styled-components';

import { Box } from '@material-ui/core';
import { SingleLineTextField } from '../../atoms/index';

const FlexBox = styled.div`
  display: flex;
  justify-content: center;
`;

export type Props = {
  email: string;
  telephoneNumber: string;
  firstName: string | null;
  lastName: string | null;
  onChangeEmail: (e: ChangeEvent<HTMLInputElement>) => void;
  onChangeTelephoneNumber: (e: ChangeEvent<HTMLInputElement>) => void;
  onChangeFirstName: (e: ChangeEvent<HTMLInputElement>) => void;
  onChangeLastName: (e: ChangeEvent<HTMLInputElement>) => void;
};

export const UpdateParent: React.FC<Props> = memo((props) => {
  const {
    email,
    firstName,
    lastName,
    telephoneNumber,
    onChangeEmail,
    onChangeFirstName,
    onChangeLastName,
    onChangeTelephoneNumber,
  } = props;

  return (
    <div>
      <FlexBox>
        <Box textAlign="center" mr={1} mb={2}>
          <SingleLineTextField
            id=""
            isFullWidth={false}
            textName=""
            placeholder=""
            value={lastName}
            onChange={onChangeLastName}
          />
        </Box>
        <Box textAlign="center" ml={1}>
          <SingleLineTextField
            id=""
            isFullWidth={false}
            textName=""
            placeholder=""
            value={firstName}
            onChange={onChangeFirstName}
          />
        </Box>
      </FlexBox>
      <Box textAlign="center" m={4}>
        <SingleLineTextField
          id=""
          isFullWidth={true}
          textName=""
          placeholder="090xxxxxxxx"
          value={telephoneNumber}
          onChange={onChangeTelephoneNumber}
        />
      </Box>
      <Box textAlign="center" m={4}>
        <SingleLineTextField
          id=""
          isFullWidth={true}
          textName=""
          placeholder="xxxx@gmail.com"
          value={email}
          onChange={onChangeEmail}
        />
      </Box>
    </div>
  );
});

UpdateParent.displayName = 'UpdateParent';
